:root {
  --bg-color: white;
  --dark-text-color: black;
  --light-text-color: white;
  --edit-code-bg: #fdf6e2;
  --view-code-bg: #fbf9f4;
  --border-color: #00000080;
  --border-radius: 4px;
}

body {
  color: var(--dark-text-color);
  background: var(--bg-color);
}

h1, h2 {
  margin-top: 1em;
  margin-bottom: .1em;
}

h3 {
  margin-top: .5em;
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.toggle {
  background: var(--bg-color);
  color: var(--dark-text-color);
  border: .5px solid #452981;
  border-radius: 6px;
  padding: .2em .4em;
}

.toggle:focus {
  border-radius: 6px;
}

.toggle.on {
  color: var(--light-text-color);
  background: #e382b1;
}

.toggle:hover {
  box-shadow: 0 0 1px 1px #de6ea4;
}

